import React from 'react'

// import css
import './Navbar.css';

//import images
import list from './list.svg'
const Navbar = () => {
  return (
    <>
      <nav>
        <a href="/" className='logo'>Team Analytics</a>
        <input type="checkbox" name="toggler" id="toggler" />
        <label htmlFor="toggler"><img src={list} alt="" /></label>
        <div className="menu">
          <ul className="list">
            <li><a href="/">Home</a></li>
          </ul>
        </div>
      </nav>
    </>
  )
}

export default Navbar